.header {
  position: fixed;
  top: 0;
  z-index: 3;
  width: 100%;
  min-height: 120px;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);

  @media (max-width: $tablet) {
    position: static;
    min-height: 100px;
  }

  @media (max-width: $mobile) {
    min-height: 70px;
  }
}

.header-container {
  box-sizing: content-box;
  display: flex;
  justify-content: space-between;
  max-width: 1160px;
  padding: 0 60px;
  margin: auto;

  @media (max-width: $tablet) {
    width: 100%;
    padding: 20px 70px 0 50px;
    box-sizing: border-box;
    justify-content: space-between;
  }

  @media (max-width: $mobile) {
    min-width: 320px;
    padding: 10px 35px 0 26px;
  }
}

.header-container > a {
  display: flex;
  text-decoration: none;
  margin-right: 30px;

  &:hover,
  &:focus-visible {
    opacity: 0.7;
    outline: none;
  }

  &:active {
    opacity: 0.5;
  }

  @media (max-width: $tablet) {
    margin: 0;
  }
}

.header__icon {
  margin-top: 30px;
  margin-right: 15px;

  @media (max-width: $tablet) {
    margin-top: 0;
  }

  @media (max-width: $mobile) {
    width: 35px;
    height: 35px;
    margin: 5px 0 0;
  }
}

.header__title-container {
  display: flex;
  flex-direction: column;
}

.header__title-container h2 {
  min-width: 145px;
  height: 25px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: bold;
  font-size: 25px;
  margin: 35px 0 5px;
  color: $base-white;

  @media (max-width: $tablet) {
    margin-top: 5px;
  }

  @media (max-width: $mobile) {
    display: none;
  }
}

.header__title-container span {
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 500;
  font-size: 12px;
  color: rgba(255, 255, 255, 0.7);

  @media (max-width: $mobile) {
    display: none;
  }
}
